import React, { Component, Fragment } from 'react'
import projects from './projects.json'
import Project from './project'

export default class index extends Component {

    getProjects = projects => {
        return projects.map(project => {
            return <Project {...project} />
        })
    }

    render() {
        return (
            <Fragment>
                <center>
                    <h1 style={{ marginBottom: '40px', color: 'black' }}>Projects</h1>
                    <p style={{ color: '#AEB6BF' }}>
                        These are some of my significant projects that are being used by the community. Most of my projects are on Artificial Intelligence, IPFS and Distributed Ledgers(DLTs).
                    </p>
                </center>
                {this.getProjects(projects)}
            </Fragment>
        )
    }
}
