import React, { Component, Fragment } from 'react'

class Podcast extends Component {
    render() {

        const { url, title } = this.props

        return (
            <div style={{ marginBottom: '32px' }}>
                <b><font size="4">{title}</font></b>
                <br />
                <iframe width="560" height="315" style={{ marginTop: '8px' }} src={url} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            </div>
        )
    }
}

export default Podcast