import React, { Component, Fragment } from 'react'
import { NavLink } from 'react-router-dom'

class Article extends Component {
    render() {

        const { backgroundImage, altText, title, subTitle, body, url } = this.props

        return (
            <Fragment>
                <div class="col-6 col-sm-6">
                    <div className="card" style={{ width: '24rem' }}>
                        <img className="card-img-top" height="255px" width="382px" src={backgroundImage} alt={altText || "Card image cap"} />
                        <div className="card-body">
                            <h5 className="card-title" style={{ height: '96px' }}>{title}</h5>
                            <h6 class="card-subtitle mb-2 text-muted">{subTitle}</h6>
                            <p className="card-text" style={{ height: '72px' }}>{body}</p>
                            <a href={url} className="btn btn-primary">Read more</a>
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default Article