import React, { Component, Fragment } from 'react'
import Experience from './experience'
import experiences from './experiences.json'

class Index extends Component {

    generateExperiences = experiences => {
        return experiences.map(experience => {
            return <Experience {...experience} />
        })
    }

    render() {
        return (
            <Fragment>
                <center style={{
                    marginBottom: '40px'
                }}>
                    <h1 style={{ marginBottom: '40px', color: 'black' }}>Experience</h1>
                    <p style={{ color: '#AEB6BF' }}>
                        I started my Entrepreneurial journey in late 2016. I started with hacking my school's website, then developing cross-platform games and Android apps. Eventually I moved into ML/AI and Distributed Ledgers world.
                    </p>
                </center>
                {this.generateExperiences(experiences)}
            </Fragment>
        )
    }
}

export default Index