import React, { Component, Fragment } from 'react'
import Article from './article'
import articles from './articles.json'

import { NavLink } from 'react-router-dom'

export default class index extends Component {

    getArticles = (articles, curIndex) => {
        return articles.slice((curIndex - 1) * 4, (curIndex) * 4).map(article => {
            return <Article {...article} />
        })
    }

    getRows = articles => {
        let rows = []
        for (let i = 0; i < articles.length; i += 2) {
            {
                rows.push(
                    <div class="row" style={{ marginBottom: '32px' }}>
                        {articles[i] ? articles[i] : null}
                        {articles[i + 1] ? articles[i + 1] : null}
                    </div>
                )
            }
        }
        return rows
    }

    render() {

        let prevBtnClass, nextBtnClass, curIndex

        const { location } = this.props

        const getCurrentIndex = () => {
            if (location.search === "") {
                curIndex = 1
            }
            else {
                curIndex = parseInt(location.search.split("=").pop())
                if (curIndex <= 0) {
                    curIndex = 1
                }
            }
        }

        getCurrentIndex()

        const getPrevBtnLink = () => {
            if (location.search === "") {
                prevBtnClass = "btn btn-secondary"
                return "/articles"
            }
            else {
                if (curIndex > 1) {
                    prevBtnClass = "btn btn-success"
                    return `/articles?page=${curIndex - 1}`
                }
                else {
                    prevBtnClass = "btn btn-secondary"
                    return "/articles"
                }
            }
        }

        const getNextBtnLink = () => {
            if (location.search === "") {
                nextBtnClass = "btn btn-success"
                return "/articles?page=2"
            }
            else {
                if (curIndex < (articles.length / 4)) {
                    nextBtnClass = "btn btn-success"
                    return `/articles?page=${curIndex + 1}`
                }
                else {
                    nextBtnClass = "btn btn-secondary"
                    return "/articles"
                }
            }
        }

        let Articles = this.getArticles(articles, curIndex)
        let Rows = this.getRows(Articles)

        return (
            <Fragment>
                <center>
                    <h1 style={{ marginBottom: '40px', color: 'black' }}>Articles</h1>
                    <p style={{ color: '#AEB6BF' }}>
                        I have very recently started making some progress with my Self-Taught Machine Learning Journey. But to be honest, it wouldnâ€™t be possible at all without the amazing community online and the great people that have helped me.
                    </p>
                </center>
                <br />
                <div class="container">
                    {Rows}
                </div>
                <div class="container" style={{ marginBottom: '32px' }}>
                    <div class="row">
                        <div class="col-sm" style={{ textAlign: "left" }}>
                            <NavLink to={getPrevBtnLink()}>
                                <button type="button" class={prevBtnClass}>â¬… Previous</button>
                            </NavLink>
                        </div>
                        <div class="col-sm" style={{ textAlign: "right" }}>
                            <NavLink to={getNextBtnLink()}>
                                <button type="button" class={nextBtnClass}>Next âž¡</button>
                            </NavLink>
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}
