import React, { Component, Fragment } from 'react'
import podcasts from './podcasts.json'
import Podcast from './podcast'

class Podcasts extends Component {

    getPodcasts = podcasts => {
        return podcasts.map(podcast => {
            return <Podcast {...podcast} />
        })
    }

    render() {
        return (
            <Fragment>
                <center>
                    <h1 style={{ marginBottom: '40px', color: 'black' }}>Podcasts</h1>
                    <p style={{ color: '#AEB6BF' }}>
                        I have very recently started making some progress with my Self-Taught Machine Learning Journey. But to be honest, it wouldnâ€™t be possible at all without the amazing community online and the great people that have helped me.
                    </p>

                    <br />
                    <span>
                        {this.getPodcasts(podcasts)}
                    </span>
                </center>
            </Fragment>
        )
    }
}

export default Podcasts