import React, { Component } from 'react'

class Project extends Component {
    render() {

        const { title, github, youtube, twitter, telegram, website, other_links, body } = this.props

        const convertToHtml = text => {
            return { __html: text };
        }

        return (
            <div style={{ marginBottom: '36px' }}>
                <span>
                    <b><font size="4">{title}</font></b>
                </span>
                <br />
                <span>
                    {
                        twitter ? <span><a href={twitter} target="_blank"><i class="fa fa-twitter" aria-hidden="true"></i></a>  </span> : null
                    }
                    {
                        github ? <span><a href={github} target="_blank"><i class="fa fa-github" aria-hidden="true"></i></a> </span> : null
                    }
                    {
                        youtube ? <span><a href={youtube} target="_blank"><i class="fa fa-youtube" aria-hidden="true"></i></a> </span> : null
                    }
                    {
                        telegram ? <span><a href={telegram} target="_blank"><i class="fa fa-telegram" aria-hidden="true"></i></a> </span> : null
                    }
                    {
                        website ? <span><a href={website} target="_blank"><i class="fa fa-globe" aria-hidden="true"></i></a> </span> : null
                    }
                    {
                        other_links ? <span><a href={other_links} target="_blank"><i class="fa fa-link" aria-hidden="true"></i></a> </span> : null
                    }
                </span>
                <br />
                {<p dangerouslySetInnerHTML={convertToHtml(body)} style={{ marginTop: '8px' }}></p>}
            </div>
        )
    }
}

export default Project